.section.mega-features::after {
    width: 0;
    height: 0;
    border-style: solid;
    margin-left: -26px;
    border-width: 26px 26px 0 26px;
    border-color: $blue transparent transparent transparent;
    position: absolute;
    top: 100%;
    left: 50%;
    display: block;
    content: ' ';
    z-index: 2;
}
.section.mega-features .container {
    text-align: center;
    background: $blue;
    padding: 75px 50px;
}
.section.mega-features .header {
    width: 100%;
    text-align: center;
}
.section.mega-features .header h2 {
    font-size: 26px;
    font-weight: bold;
    line-height: 1.25;
    text-align: center;
    color: $white;
}
@media (min-width: $(desktop)) {
    .section.mega-features .header h2 {
        font-size: 32px;
    }
}
.section.mega-features .header .subtitle {
    font-size: 21px;
    font-weight: 600;
    font-style: italic;
    line-height: 1.9;
    letter-spacing: -0.2px;
    text-align: center;
    color: $white;
}
.section.mega-features .features {
    width: 100%;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 30px 0;
}
.section.mega-features .features li {
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 auto;
        -ms-flex: 0 0 auto;
            flex: 0 0 auto;
    width: calc(99.99% * 1/1 - (30px - 30px * 1/1));
    padding: 0 20px;
}
.section.mega-features .features li:nth-child(1n) {
    margin-right: 30px;
    margin-left: 0;
}
.section.mega-features .features li:last-child {
    margin-right: 0;
}
.section.mega-features .features li:nth-child(1 '' ''n) {
    margin-right: 0;
    margin-left: auto;
}
@media (min-width: $(desktop)) {
    .section.mega-features .features li {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 1/3 - (30px - 30px * 1/3));
    }
    .section.mega-features .features li:nth-child(1n) {
        margin-right: 30px;
        margin-left: 0;
    }
    .section.mega-features .features li:last-child {
        margin-right: 0;
    }
    .section.mega-features .features li:nth-child(3 '' ''n) {
        margin-right: 0;
        margin-left: auto;
    }
}
.section.mega-features .features li .icon {
    color: $white;
    font-size: 60px;
}
.section.mega-features .features li .title {
    font-family: 'Open Sans';
    font-size: 14px;
    font-weight: 600;
    line-height: 1.5;
    letter-spacing: 0.7px;
    text-align: center;
    color: $white;
    text-transform: uppercase;
    margin-bottom: 15px;
}
.section.mega-features .features li .body {
    font-family: 'Open Sans';
    font-size: 15px;
    font-weight: 600;
    line-height: 1.33;
    text-align: center;
    color: $white;
}

